import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Indeex from '@/components/indeex'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },{
      path: '/indeex',
      name: 'indeex',
      component: Indeex
    }
  ]
})



