<template>
  <div class="index">
    <router-link :to="{ name: 'indeex', params: {} }"><img src="./../assets/logo.png"></router-link>
    <h1>{{ msg }}</h1>
    <h3>linkTo: <a href="http://hungking.cc">indeex</a></h3>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      msg: 'Welcome to my App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
h2 ,h3 {
  font-weight: normal;
  a {
    color: #42b983;
  }
  a:link ,a:visited{
    text-decoration: none;
  }
}
</style>



