<template>
  <div class="indeex" id="indeex">
    <!-- test page. -->
  </div>
</template>

<script>
import * as THREE from 'three';
import GLTFLoader from 'three-gltf-loader';
import FBXLoader from 'three-fbx-loader';
import OBJLoader  from 'three-obj-loader';
import OrbitControls from 'three-orbitControls';
OBJLoader(THREE);
console.log(THREE);
let indeex = null;
let w = 0;
let h = 0;
let scene = null;
let loaderScene = null;
let camera = null;
let directionalLight = null;
let pointLight = null;
let ambient = null;
let renderer = null;
let orbitControls = null;
let plane = null;
let cube = null;
let radian = (Math.PI / 180);

export default {
  name: 'indeex',
  data () {
    return {
      msg: 'indeex'
    }
  },
  mounted(){
    this.indeex = document.querySelector('#indeex');

    this.getInitWH();
    this.init();

    window.addEventListener('resize',this.resize);
  },
  methods: {
    init(){
      console.log('init...');
      this.initScene();
      this.initCamera();
      this.initRenderer();
      this.initAxesHelper(5);
      this.initLight();
      this.initSceneLoader();
      this.initPlane();
      this.initObj();
      this.initOrbitControls();
      this.initRender();
    },
    initScene(){
      let scene = new THREE.Scene();
      this.scene = scene;
    },
    initCamera(){
      let camera = new THREE.PerspectiveCamera(60, this.w / this.h, .1, 1000);
      camera.position.x = 8;
      camera.position.y = 30;
      camera.position.z = 8;
      camera.rotation.y = 45 * radian;
      this.camera = camera;
    },
    initLight(){
      let ambient = new THREE.AmbientLight(0xffffff,1);
      this.scene.add(ambient);
      this.ambient = ambient;

      // let directionalLight = new THREE.DirectionalLight(0xffffff, 1);
      // directionalLight.position.set(0, 2, 0);
      // this.directionalLight = directionalLight;
      // this.scene.add(directionalLight);

      // let pointLight = new THREE.PointLight(0xffffff, 20, 22, .1);
      // pointLight.position.set(0,20,0);
      // this.pointLight = pointLight;
      // this.scene.add(pointLight);
    },
    initRenderer(){
      let renderer = new THREE.WebGLRenderer();
      renderer.setSize(this.w,this.h);
      // console.log('get dom:',document.querySelector('#indeex'));
      this.indeex.appendChild(renderer.domElement);
      this.renderer = renderer;
    },
    initPlane(){
      var planceGeometry = new THREE.PlaneGeometry(10, 10);
      var planeMaterial = new THREE.MeshLambertMaterial({ color: 0x949391 });
      var plane = new THREE.Mesh(planceGeometry, planeMaterial);
      plane.receiveShadow = true;
      plane.rotation.x = -0.5*Math.PI;
      plane.position.x = 0;
      plane.position.y = -.1;
      plane.position.z = 0;
      this.scene.add(plane);
      this.plane = plane;
    },
    initObj(){
      let geometry = new THREE.BoxGeometry(1, 1, 1);
      let material = new THREE.MeshBasicMaterial({color:0x0000ff});
      let cube = new THREE.Mesh(geometry, material);
      cube.position.x = 0;
      cube.position.y = .5;
      cube.position.z = 0;
      this.cube = cube;
      // this.scene.add(cube);
    },
    initSceneLoader(){
      let self = this;
      let manager = new THREE.LoadingManager();
			manager.onProgress = function ( item, loaded, total ) {
				console.log( item, loaded, total );
			};


      // let loader = new FBXLoader();
      // loader.load('http://127.0.0.1/assets/Underground bunker/Bunker07Anim.fbx', function (object3d) {
      //   console.log('loader fbx: ',object3d)
      //   this.scene.add(object3d);
      // });


      let path = 'http://www.hungking.cc/assets/models/underground_bunker/textures/';
			let format = '.jpg';
			let envMap = new THREE.CubeTextureLoader().load( [
				path + 'Texture-base_baseColor' + format, path + 'Texture-base-gloss-jpg_baseColor' + format
				// path + 'posy' + format, path + 'negy' + format,
				// path + 'posz' + format, path + 'negz' + format
			] );

      let loader = new GLTFLoader();
			loader.load( 'http://www.hungking.cc/assets/models/underground_bunker/scene.gltf', function ( gltf ) {
        console.log('load scene: ',gltf);
				gltf.scene.traverse( function ( child ) {
					if ( child.isMesh ) {
						// child.material.envMap = envMap;
					}
				} );
				self.scene.add( gltf.scene );
			} );
      this.loaderScene = loader;

      // let textureLoader = new THREE.TextureLoader( manager );
			// let texture = textureLoader.load( 'http://127.0.0.1/assets/dfcj/东方场景01(1).jpg' );
      // let loader = new THREE.OBJLoader( manager );
			// 	loader.load( 'http://127.0.0.1/assets/dfcj/OBJ/东方场景01.obj', function ( object ) {
			// 		object.traverse( function ( child ) {
			// 			if ( child instanceof THREE.Mesh ) {
			// 				child.material.map = texture;
			// 			}
			// 		} );
      //     object.position.x = 0;
			// 		object.position.y = 0;
      //     object.position.z = 0;
			// 		self.scene.add( object );
			// 	}, function(progress){
      //     console.log('progress: ',progress);
      //   }, function(err){
      //     console.log('loader obj err: ',err);
      //   } );
    },
    initRender(){
      // this.cube.rotation.x += .05;
      this.cube.rotation.y += .05;
      requestAnimationFrame(this.initRender);
      this.renderer.render(this.scene,this.camera);
    },
    initAxesHelper(num = 20){
      let axes = new THREE.AxesHelper(num);    // axes坐标轴对象
      this.scene.add(axes);
    },
    initOrbitControls(){
      let controls = new OrbitControls(this.camera, this.renderer.domElement);
      controls.enableDamping = true;
      controls.dampingFactor = 0.25;
      controls.enableZoom = false;
      this.orbitControls = controls;
    },
    resize(e){
      let win = e.target;
      this.w = win.document.body.clientWidth;
      this.h = win.document.body.clientHeight;

      this.reSetThree();
    },
    getInitWH(){
      this.w = window.document.body.clientWidth;
      this.h = window.document.body.clientHeight;
      console.log('browser width:',this.w,' browser height:', this.h);
    },
    reSetThree(){
      this.camera.aspect = this.w / this.h;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(this.w,this.h);
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.indeex{
  width: 100vw;
  height: 100vh;
  background-color: #F1F1F1;
}
</style>



